	export default {
		data() {
			return {
				list: [],
				loading: false,
				finished: false
			}
		},
		onLoad() {

			this.init()

		},
    methods: {
		/**
		 * 模拟数据更新
		 */
		init() {
			// 这里包含了滚动加载，文档地址 https://youzan.github.io/vant/#/zh-CN/list
			if (this.finished) {
				return
			}
			// 异步更新数据
			setTimeout(() => {
				for (let i = 0; i < 10; i++) {
					this.list.push({
						name: this.list.length + 1,
						type: Math.random() * 10 > 5 ? '1': '2'
					});
				}
				// 加载状态结束
				this.loading = false;

				// 数据全部加载完成
				if (this.list.length >= 40) {
					this.finished = true;
				}
		    }, 500);
		},
		/**
		 * 去耗材详情页面
		 * @param {any} 传入的对应耗材的信息  
		 */
		goLabDetails(item) {
			console.log('传入的耗材的信息是', item)
			uni.navigateTo({
				url: `./consumables-details?item=${item.name}&type=${item.type}`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
		
    }
	}
